import React from "react";
import { Box } from "@mui/material";
import { Outlet } from "react-router-dom";
import AuthLayout from "../AuthLayout";

export const LoginLayout = ({ isProtected = true }) => {
  const LayoutWrapper = isProtected ? AuthLayout : React.Fragment;

  return (
    <LayoutWrapper>
      <Box>
        <Outlet />
      </Box>
    </LayoutWrapper>
  );
};
