import { createAction } from 'redux-actions';
import * as types from './types';

export const loginStart = createAction(types.LOGIN.START, (payload) => payload);

export const loginSuccess = createAction(types.LOGIN.SUCCESS, (auth) => auth);

export const loginError = createAction(types.LOGIN.ERROR, (error) => error);

export const initUserMenuState= {
  auth_info: {},
};