import React, { useContext, useEffect, useState } from "react";
import { Box, Button, Typography } from "@mui/material";
import { makeStyles } from "@mui/styles";
import { Google } from "@mui/icons-material";
import { GoogleLogin } from "react-google-login";
// import { useDispatch } from "react-redux";
// import { loginStart } from "../../store/auth/actions.js";
import { useNavigate } from "react-router-dom";
import { AppContext } from "../../context";

export const Login = () => {
  const classes = useStyles();
  const navigate = useNavigate();
  const { setUserInfo } = useContext(AppContext);
  // const dispatch = useDispatch();
  const [abc, setAbc] = useState(0);
  const responseGoogle = (response) => {
    // dispatch(loginStart(response));

    if (response?.accessToken) {
      localStorage.setItem("user", JSON.stringify(response));
      localStorage.setItem("jwt", JSON.stringify(response.accessToken));
      navigate("/");
      setUserInfo(response);
    }
  };
  useEffect(() => {
    console.log("abc");
    return "dev";
  }, []);
  useEffect(() => {
    setTimeout(() => {
      setAbc(1);
    }, 5000);
  }, []);

  if (abc === 1)
    return (
      <Box className={classes.root}>
        <Box className={classes.boxLogin}>
          <Typography className={classes.boxLoginTitle}>
            Login for Project X
          </Typography>
          <Box className={classes.boxLoginButton}>
            <GoogleLogin
              clientId="276034369117-h6cfooe2g8g92jm11flh0juk2livbgn7.apps.googleusercontent.com"
              render={(renderProps) => (
                <Button
                  onClick={renderProps.onClick}
                  disabled={renderProps.disabled}
                  className={classes.buttonLogin}
                >
                  <Google />
                  Login with Google
                </Button>
              )}
              className={classes.buttonLogin}
              buttonText="Login with Google"
              onSuccess={responseGoogle}
              onFailure={responseGoogle}
              cookiePolicy={"single_host_origin"}
            />
            {/* <GoogleLogout
            clientId="276034369117-fchc2rt7lnkapsfnu6akb7e76sdk21tk.apps.googleusercontent.com"
            buttonText="Logout"
            onLogoutSuccess={responseGoogle}
          ></GoogleLogout> */}
          </Box>
        </Box>
      </Box>
    );
  return <>abc</>;
};
