import React, { Fragment } from "react";

import { BrowserRouter, Routes, Route } from "react-router-dom";
import { routes } from "./routes";
import "./App.scss";
import { AppContextProvider } from "./context";
export default function App() {
  return (
    <AppContextProvider>
      <BrowserRouter>
        <Fragment>
          <Routes>
            {routes.map(
              ({ Element, Layout, Protected, children, path }, key) => {
                return (
                  <Route key={key} element={<Layout protected={Protected} />}>
                    <Route path={path} element={<Element />} />
                  </Route>
                );
              }
            )}
          </Routes>
        </Fragment>
      </BrowserRouter>
    </AppContextProvider>
  );
}
